.LoaderBackdrop {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.LoaderContent {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  display: flex;
}

.ldsLoader {
  color: #fff;
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.ldsLoader div {
  transform-origin: 40px 40px;
  animation: ldsLoader 1.2s linear infinite;
}
.ldsLoader div:after {
  content: " ";
  display: block;
  position: absolute;
  top: 3px;
  left: 37px;
  width: 6px;
  height: 18px;
  border-radius: 20%;
  background: #fff;
}
.ldsLoader div:nth-child(1) {
  transform: rotate(0deg);
  animation-delay: -1.1s;
}
.ldsLoader div:nth-child(2) {
  transform: rotate(30deg);
  animation-delay: -1s;
}
.ldsLoader div:nth-child(3) {
  transform: rotate(60deg);
  animation-delay: -0.9s;
}
.ldsLoader div:nth-child(4) {
  transform: rotate(90deg);
  animation-delay: -0.8s;
}
.ldsLoader div:nth-child(5) {
  transform: rotate(120deg);
  animation-delay: -0.7s;
}
.ldsLoader div:nth-child(6) {
  transform: rotate(150deg);
  animation-delay: -0.6s;
}
.ldsLoader div:nth-child(7) {
  transform: rotate(180deg);
  animation-delay: -0.5s;
}
.ldsLoader div:nth-child(8) {
  transform: rotate(210deg);
  animation-delay: -0.4s;
}
.ldsLoader div:nth-child(9) {
  transform: rotate(240deg);
  animation-delay: -0.3s;
}
.ldsLoader div:nth-child(10) {
  transform: rotate(270deg);
  animation-delay: -0.2s;
}
.ldsLoader div:nth-child(11) {
  transform: rotate(300deg);
  animation-delay: -0.1s;
}
.ldsLoader div:nth-child(12) {
  transform: rotate(330deg);
  animation-delay: 0s;
}
@keyframes ldsLoader {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
